*{
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

a{
    text-decoration: none;
    color: #454545;
    font-size: 12px;
}

body {
    margin: 0;
    padding: 60px 15px;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    background: #fff
}

textarea {
    resize: none;
    width: 100%;
    min-height: 100px;
    margin-top: 20px;
    padding: 10px;
    font-family: sans-serif;
    border-radius: 5px;
    outline: none
}

div.grup-kiri{
    float: left;
    height: 200px;
    width: 300px;
    display: block;
    margin-right: 20px;
}

div.grup-kanan{
    float: left;
    width: 300px;
}

div.div-atas, 
div.div-bawah{
    border: 1px solid #e0e0e0;
    border-radius: 2px;
    padding: 10px 10px 20px;
    overflow: hidden
}

div.div-bawah{
    margin-top: 5px;
}

div.div-bawah.mh-10{
    min-height: 80px;
}

div.div-bawah.kanan{
    text-align: right
}

div.div-bawah.bg01{
    background: #f0f0f0;
    border:none;
}

.dialog-action.bawah{
    background: #f0f0f0;
    height: 64px;
    line-height: 64px;
    text-align: right;
    padding: 0 16px
}

div.div-header{
    background-color: #337ab7;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    padding: 16px 24px;
    color: #fff;
    margin-bottom: 20px;
}



/* Form Login */

div.login{
    width: 678px;
    margin: 7% auto;
    padding: 20px;
    border-radius: 5px;
    overflow: hidden;
    background: #03A9F4;
    min-height: 350px;
}

div.login-kiri,
div.login-kanan{
    width: 50%;
    float: left;
    height: 310px;
    border-radius: 5px;
}

div.login-kiri{
    text-align: right;
    padding-right: 20px;
    color: white;
}

h1{
    font-size: 22px;
}

div.login-kanan{
    padding: 20px 20px 0;
    height: auto;
    overflow: hidden;
    background: #fff;
    text-align: center;
    -webkit-box-shadow: 0 1px 10px 2px rgba(0,0,0,0.1);
            box-shadow: 0 1px 10px 2px rgba(0,0,0,0.1);
}

div.login-kanan h1{
    text-align: left;
    padding: 0 5px;
}

div.login-kanan p{
    color: #696969;
    font-size: 14px;
    line-height: 22px;
    text-align: left;
    padding: 0 5px;
    text-align: justify
}

ul.copyright{
    text-align: center;
    margin-top: 3%;
    overflow: hidden;
    display: inline-block;
    width: 50%;
    float: right;
    padding: 0 16px;
}

ul.copyright li{
    list-style: none;
    color: #ffffff;
    font-size: 12px;

}


